export function getAngleByDirection(direction){
    switch (direction) {
        case "North":
            return 0;
        case "East":
            return 90;
        case "South":
            return 180;
        case "West":
            return -90;
        case "NorthWest":
            return -45;
        case "NorthEast":
            return 45;
        case "SouthWest":
            return -135;
        case "SouthEast":
            return 135;
    }
    return 0;
}

export function transformAngle(angle) {
    if (angle > 180 )
        return angle - 360;

    if (angle < -180 )
        return angle + 360;

    if (angle === 180 || angle === -180)
        return 180;

    return angle;
}

export function getDirectionByAngle(angle){
    angle = transformAngle(angle);

    switch (angle) {
        case 0:
            return gameButtons.gunner.North;
        case 90:
            return gameButtons.gunner.East;
        case 180:
            return gameButtons.gunner.South;
        case -90:
            return gameButtons.gunner.West;
        case -45:
            return gameButtons.gunner.NorthWest;
        case 45:
            return gameButtons.gunner.NorthEast;
        case -135:
            return gameButtons.gunner.SouthWest;
        case 135:
            return gameButtons.gunner.SouthEast;
    }
    return "undefined";
}

